  }
  50%{
    border-bottom-color: white;
    border-right-color: white;
  }
  75%{
    border-bottom-color: #63D471;
    border-left-color: #233329;
  }
  100%{
    border-top-color: white;
    border-bottom-color: white;
    border-left-color: #233329;
    border-right-color: #233329;
  }
}

@keyframes boxDecoration {
  0%{
    border-top-color: white;
    border-bottom-color: white;
    border-left-color: #233329;
    border-right-color: #233329;
  }
  25%{
    border-top-color: #63D471;
    border-left-color: white;
  }
  50%{
    border-bottom-color: white;
    border-right-color: white;
  }
  75%{
    border-bottom-color: #63D471;
    border-left-color: #233329;
  }
  100%{
    border-top-color: white;
    border-bottom-color: white;
    border-left-color: #233329;
    border-right-color: #233329;
  }
}
